import { Component, OnInit } from '@angular/core';
import { TestSuite } from '../../../../entity';
import { TestSuitesService } from '../../../services';
import { interval } from '../../../../../node_modules/rxjs';

@Component({
  selector: 'app-dashboard-queue',
  templateUrl: './dashboard-queue.html',
  styleUrls: ['./dashboard-queue.css'],
})
export class DashboardQueueComponent implements OnInit {

  testSuites: TestSuite[] = [];

  prevTestSuite: TestSuite = {};
  currTestSuite: TestSuite = {};
  nextTestSuite: TestSuite = {};

  private _index: number = 0;
  set index(value: number) {
    this._index = (value < 0) ? (this.testSuites.length - 1) : 
                    (value >= this.testSuites.length) ? 0 : value;
    
    this.currTestSuite = this.testSuites[this._index];
    this.prevTestSuite = this.testSuites[
      (this._index == 0) ? (this.testSuites.length - 1) : (this._index - 1)
    ];
    this.nextTestSuite = this.testSuites[
      (this._index == this.testSuites.length - 1) ? 0 : (this._index + 1) 
    ];
  }
  get index() {
    return this._index;
  }

  
  constructor(
    private testSuitesService: TestSuitesService
  ) {}

  ngOnInit() {
    this.load();
  }

  load() {
    this.testSuitesService.getAll().then(testSuites => {
      this.testSuites = testSuites;
      this.index = 0;

      const source = interval(3000);
      const subscribe = source.subscribe(() => {
        this.index = this.index + 1;
        console.log(this.index);  
      });

    }).catch(err => {
      console.log(err);
    });
  }
}
